.wrapper {
  background-color: rgb(145, 145, 145);
  box-shadow: inset 0px 0px 15px black;
  width: 1300px;
  margin: auto;
  border-radius: 5px;
  display: grid;
  padding: 15px;
  grid-template-columns: 2fr 10fr;
  grid-template-rows: 1fr auto;
  grid-template-areas:
    'h h'
    's c';
    opacity: 0.95;
}

.app-wrapper-content {
    grid-area: c;
}
